 ImgList: '',\n      rotateData: 0,\n      iconfont: false,\n      positionX: 0,\n      positionY: 0\n    }\n  },\n  directives: {\n    drag: {\n      // 指令的定义\n      bind: function (el) {\n        //获取当前元素\n        var odiv = el.querySelector('.el-dialog')\n        odiv.onmousedown = (e) => {\n          //算出鼠标相对元素的位置\n          let disX = e.clientX - odiv.offsetLeft;\n          let disY = e.clientY - odiv.offsetTop;\n          document.onmousemove = (e) => {\n            //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置\n            let left = e.clientX - disX;\n            let top = e.clientY - disY;\n            //绑定元素位置到positionX和positionY上面\n            //              this.positionX = top;\n            //              this.positionY = left;\n            //移动当前元素\n            odiv.style.marginLeft = left + \"px\"\n            odiv.style.marginTop = top + 'px';\n          };\n          document.onmouseup = (e) => {\n            document.onmousemove = null;\n            document.onmouseup = null;\n          };\n        };\n      }\n    }\n  },\n  methods: {\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/PolicyAudit/adoptList', { token: this.token, page: this.page }).then(res => {\n        if (res.data.code == 1) {\n\n          this.list = res.data.data.list\n          this.pageCount = res.data.data.rows;\n        } else {\n          layer.msg(res.data.msg, { time: 1500 })\n        }\n      }).catch(err => console.log(err))\n    },\n    //查看未审核列表详情\n    details (pid, uid) {\n\n      this.UnauditedID = {\n        token: this.token,\n        pid: pid,\n        uid: uid\n      }\n      this.detailsVisible = true\n      this.$axios.post('admin/PolicyAudit/detail', { token: this.token, pid: pid }).then(res => {\n        if (res.data.code == 1) {\n\n          this.information = res.data.data\n        }\n      }).catch(err => console.log(err))\n    },\n    enlarge () {\n      const ViewerDom = this.$refs.adopt\n      const viewer = new Viewer(ViewerDom, {});\n    }\n\n  },\n  mounted () {\n    this.init()\n\n  }\n}\n</script>\n<style scoped>\n>>> .viewer-canvas {\n  z-index: 9999;\n}\n>>> .el-dialog__wrapper {\n  overflow: hidden;\n}\n\n.table a {\n  color: #04bbfc;\n}\n\n.details {\n  font-size: 12px;\n}\n.information td {\n  text-align: left;\n}\n.details table td {\n  font-size: 12px;\n  border: none;\n}\n.Insurancetype {\n  margin-top: 20px;\n}\n.Insurancetype table {\n  border: 1px solid #dbdbdb;\n}\n.Insurancetype table th {\n  background: none;\n  width: 50%;\n  border: 1px solid #dbdbdb;\n  font-size: 16px;\n}\n.Insurancetype table td {\n  border: 1px solid #dbdbdb;\n}\n.Photo {\n  text-align: center;\n  margin-top: 20px;\n  overflow-x: auto;\n}\n\n.Photo > ul {\n  margin: 0 auto;\n  display: flex;\n  justify-content: center;\n}\n\n.Photo img {\n  text-align: center;\n  max-height: 100px;\n  max-height: 100px;\n  width: 100px;\n  height: 100px;\n  padding-left: 10px;\n}\n.content > textarea {\n  border: none;\n  width: 100% !important;\n  resize: none;\n  height: 100px;\n  max-height: 300px !important;\n}\n.total:after {\n  display: block;\n  content: \"\";\n  clear: both;\n}\n\n.right {\n  float: right;\n  margin: 20px 0;\n  font-size: 18px;\n  color: #04bbfc;\n}\n</style>\n<style>\n.adopt .el-dialog__header {\n  background: none;\n  padding-bottom: 20px;\n  border-bottom: 1px solid #e6e6eb;\n}\n.adopt .el-dialog__footer {\n  border-top: 1px solid #e6e6eb;\n  padding-bottom: 10px;\n}\n.Reason .el-dialog__header {\n  padding-bottom: 20px;\n  border-bottom: 1px solid #e6e6eb;\n}\n.Reason .el-dialog__footer {\n  border-top: 1px solid #e6e6eb;\n  padding-bottom: 10px;\n}\n</style>\n"],"sourceRoot":""}]);

